import { configureStore } from '@reduxjs/toolkit'
import { connectRouter, routerMiddleware } from 'connected-react-router'
import { createBrowserHistory } from 'history'

import corridorReducer from './slices/Corridor.slice'
import routeReducer from './slices/Route.slice'
import shelterReducer from './slices/Shelter.slice'
import detailReducer from './slices/Detail.slice'
import locationReducer from './slices/Location.slice'
import viewReducer from './slices/View.slice'
import flagReducer from './slices/Flag.slice'
import nomiReducer from './slices/Nomi.slice'

export const history = createBrowserHistory()

const rootReducer = (history) => ({
    router: connectRouter(history),
    corridor: corridorReducer,
    route: routeReducer,
    shelter: shelterReducer,
    detail: detailReducer,
    location: locationReducer,
    view: viewReducer,
    flag: flagReducer,
    nomi: nomiReducer
})

export const store = configureStore({
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware().concat(routerMiddleware(history)),
    reducer: rootReducer(history)
})
