import axios from 'axios'
import { createAsyncThunk } from '@reduxjs/toolkit'
import { API_URL } from '../config/default.config'
import { requestHeaders } from '../utils/helper.util'

const SERVICE_ROOT = `${API_URL}/corridors`

// status: working as intended
export const get = createAsyncThunk(
    'corridor/get',
    async (payload) =>
        await (
            await axios.get(SERVICE_ROOT, requestHeaders)
        ).data.data.data
)

