import axios from 'axios'
import { createAsyncThunk } from '@reduxjs/toolkit'
import { API_URL } from '../config/default.config'
import { requestHeaders } from '../utils/helper.util'

const SERVICE_ROOT = `${API_URL}/routes`

// status: not yet tested
export const get = createAsyncThunk(
    'route/get',
    async (payload) =>
        await (
            await axios.get(
                `${SERVICE_ROOT}/${payload.origin}/${payload.target}`,
                requestHeaders
            )
        ).data
)
