import { createSlice } from '@reduxjs/toolkit'

export const detailSlice = createSlice({
    name: 'detail',
    initialState: {
        data: {},
        flag: {
            isCorridor: false,
            isRoute: false,
            isShelter: false
        }
    },
    reducers: {
        set: (state, action) => {
            state.data = action.payload
        },
        clear: (state) => {
            state.data = {}
        },
        setFlag: (state, action) => {
            switch (action.payload) {
                case 'corridor':
                    state.flag = {
                        isCorridor: true,
                        isRoute: false,
                        isShelter: false
                    }
                    break
                case 'route':
                    state.flag = {
                        isCorridor: false,
                        isRoute: true,
                        isShelter: false
                    }
                    break
                case 'shelter':
                    state.flag = {
                        isCorridor: false,
                        isRoute: true,
                        isShelter: false
                    }
                    break
                default:
                    state.flag = {
                        isCorridor: false,
                        isRoute: false,
                        isShelter: false
                    }
                    break
            }
        },
        clearFlag: (state) => {
            state.flag = {
                isCorridor: false,
                isRoute: false,
                isShelter: true
            }
        }
    }
})

export const selectDetail = {
    data: (state) => state.detail.data,
    flag: (state) => {
        const { isCorridor, isRoute, isShelter } = state.detail.flag
        return isCorridor
            ? 'corridor'
            : isRoute
            ? 'route'
            : isShelter
            ? 'shelter'
            : 'none'
    }
}

export const { set, clear, setFlag, clearFlag } = detailSlice.actions

export default detailSlice.reducer
