import {
    Container,
    Button,
    ButtonGroup,
    Card,
    Offcanvas
} from 'react-bootstrap'
import {
    MapContainer,
    TileLayer,
    Marker,
    Popup,
    ZoomControl
} from 'react-leaflet'
import L from 'leaflet'
import { useEffect, useRef, useState } from 'react'
import { useHistory, useRouteMatch, Switch, Route } from 'react-router'

const useTitle = (title, prevailOnMount = false) => {
    const defaultTitle = useRef(document.title)
    useEffect(() => {
        document.title = title
    }, [title])
    useEffect(
        () => () => {
            if (!prevailOnMount) {
                document.title = defaultTitle.current
            }
        },
        []
    )
}

const Additional = (props) => {
    return (
        <Offcanvas show={props.show} onHide={props.onHide}>
            <Offcanvas.Header closeButton>
                <Offcanvas.Title>Example Title</Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>Example Body</Offcanvas.Body>
        </Offcanvas>
    )
}

const Navigation = () => {
    const [dataAdd, setAdd] = useState(false)
    const handleAddShow = () => setAdd(true)
    const handleAddHide = () => setAdd(false)
    return (
        <Card>
            <Card.Body>
                <ButtonGroup className='w-100'>
                    <Button className='shadow-none' onClick={handleAddShow}>MB</Button>
                    <Button className='shadow-none' disabled>BRT</Button>
                    <Button className='shadow-none' >SH</Button>
                    <Button className='shadow-none' >CO</Button>
                    <Button className='shadow-none' >RO</Button>
                </ButtonGroup>
                <Additional show={dataAdd} onHide={handleAddHide}></Additional>
            </Card.Body>
        </Card>
    )
}

const Sidebar = () => {
    const ref = useRef(null)
    const [dataHide, setHide] = useState(false)

    const history = useHistory()
    const match = useRouteMatch('/brt-route/map-test/:sample')
    const { url: path } = useRouteMatch()
    const { sample } = match ? match.params : ''

    const handleNav = (props) => history.push(path + '/' + props)
    const back = () => history.goBack()

    const handleHide = () => {
        setHide(!dataHide)
    }

    useEffect(() => {
        L.DomEvent.disableClickPropagation(ref.current)
        L.DomEvent.disableScrollPropagation(ref.current)
    })
    return (
        <Container
            className='leaflet-sidebar position-absolute top-0 pt-2'
            ref={ref}>
            <Navigation />
            <Card className='mt-2'>
                <Card.Body>
                    <ButtonGroup className='w-100'>
                        <Button className='shadow-none' onClick={back}>Back</Button>
                        <Button className='shadow-none' onClick={() => handleNav('boom')}>Boom</Button>
                        <Button className='shadow-none' onClick={() => handleNav('pow')}>Pow</Button>
                        <Button className='shadow-none' onClick={() => handleNav('bruh')}>Bruh</Button>
                        <Button className='shadow-none' onClick={handleHide}>Hide</Button>
                    </ButtonGroup>
                </Card.Body>
            </Card>
            {sample && (
                <Card className='mt-2'>
                    <Card.Body>
                        <h1>{sample}</h1>
                    </Card.Body>
                </Card>
            )}
            {!dataHide && (
                <Card className='mt-2'>
                    <Card.Body>
                        <h1>Something to hide</h1>
                    </Card.Body>
                </Card>
            )}
        </Container>
    )
}

const MapTest = () => {
    useTitle('Map Test')
    return (
        <MapContainer
            center={[51.505, -0.09]}
            zoom={13}
            scrollWheelZoom={false}
            zoomControl={false}>
            <ZoomControl position={'bottomright'}></ZoomControl>
            <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
            />
            <Marker position={[51.505, -0.09]}>
                <Popup>
                    A pretty CSS3 popup. <br /> Easily customizable.
                </Popup>
            </Marker>
            <Sidebar />
        </MapContainer>
    )
}

export default MapTest
