import React from 'react'
import { Card } from 'react-bootstrap'

import PaneDetail from './Detail/PaneDetail'
import PaneList from './List/PaneList'

const PaneContent = ({ match }) => {
    // console.log('pane content')

    return (
        <Card
            className={
                'sidebar__body animate__animated animate__fadeIn border-0'
            }>
            <Card.Body>
                <div className='sidebar__menu'></div>
                {match.params.type ? (
                    match.params.uuid || match.params.routeId ? (
                        <PaneDetail match={match} />
                    ) : (
                        <PaneList match={match} />
                    )
                ) : (
                    <div className='base'>BASE</div>
                )}
            </Card.Body>
        </Card>
    )
}

const CompMemo = React.memo(PaneContent)

export { CompMemo as default }
