import axios from 'axios'
import { createAsyncThunk } from '@reduxjs/toolkit'
import { requestHeaders } from '../utils/helper.util'

const SERVICE_ROOT = `https://nominatim.openstreetmap.org`

export const status = createAsyncThunk(
    'nomi/status',
    async (payload) =>
        await (
            await axios.get(`${SERVICE_ROOT}/status.php`, {
                params: {
                    format: 'json'
                }
            })
        ).data
)
export const search = createAsyncThunk(
    'nomi/search',
    async (payload) =>
        await (
            await axios.get(`${SERVICE_ROOT}/search`, {
                params: {
                    format: 'json',
                    country: 'Indonesia',
                    countrycodes: 'id',
                    q: payload.query
                }
            })
        ).data
)
export const reverse = createAsyncThunk('nomi/reverse', async (payload) => {
    await axios.get(``, requestHeaders)
})
