import React from 'react'
import { Card } from 'react-bootstrap'

import Corridor from './Corridor'
import Route from './Route'
import Shelter from './Shelter'

const PaneDetail = ({ match }) => {
    // console.log('pane detail')

    return (
        <Card
            className={
                'sidebar__details animate__animated animate__fadeIn border-0'
            }>
            <Card.Body className='p-0'>
                {match.params.type === 'corridors' && <Corridor />}
                {match.params.type === 'shelters' && <Shelter />}
                {match.params.type === 'routes' && <Route />}
            </Card.Body>
        </Card>
    )
}

const CompMemo = React.memo(PaneDetail)

export { CompMemo as default }
