export const getByID = createAsyncThunk(
    'shelter/getByID',
    async (payload) =>
        {
            console.log('shelter/getbyid')
            return await (
                await axios.get(
                    `${SERVICE_ROOT}/${payload.params.id}`,
                    requestHeaders
                )
            ).data.data
        }
)

