import React from 'react'
import { Col, ListGroup, Row } from 'react-bootstrap'

const Route = (props) => {
    // console.log('pane list route')

    return (
        <div className='sidebar__list__route animate__animated animate__fadeIn'>
            <ListGroup>
                <ListGroup.Item className='sidebar__list__item animate__animated animate__fadeIn'>
                    <Row className='gx-0 gy-1'>
                        <Col>
                            <div className='sidebar__list__body ms-1 text-center text-sm-start'>
                                <h6 className='sidebar__list__title mb-1'>
                                    Halte Hyundai - TPB Blok O
                                </h6>
                                <h6 className='sidebar__list__subtitle text-muted small mb-0'>
                                    2x Trasit · 20 Menit · 2 Km
                                </h6>
                            </div>
                        </Col>
                    </Row>
                </ListGroup.Item>
            </ListGroup>
        </div>
    )
}

const CompMemo = React.memo(Route)

export { CompMemo as default }
