import React from 'react'
import { Badge, Col, ListGroup, Row } from 'react-bootstrap'

const Shelter = (props) => {
    // console.log('pane list shelter')

    return (
        <div className='sidebar__list__shelter animate__animated animate__fadeIn'>
            <ListGroup>
                <ListGroup.Item className='sidebar__list__item animate__animated animate__fadeIn'>
                    <Row className={'gx-0 gy-1'}>
                        <Col sm={2} xs={12}>
                            <h5 className='sidebar__list__header me-sm-2 mb-0'>
                                <Badge bg='danger'>10</Badge>
                            </h5>
                        </Col>
                        <Col>
                            <div className='sidebar__list__body ms-1 text-center text-sm-start'>
                                <h6 className='sidebar__list__title mb-1'>
                                    Hyundai Bandara Adi Sutjipto
                                </h6>
                                <h6 className='sidebar__list__subtitle text-muted small mb-0'>
                                    Shelter · Trans Jogja
                                </h6>
                            </div>
                        </Col>
                    </Row>
                </ListGroup.Item>
            </ListGroup>
        </div>
    )
}

const CompMemo = React.memo(Shelter)

export { CompMemo as default }
