import { createSlice } from '@reduxjs/toolkit'

export const locationSlice = createSlice({
    name: 'location',
    initialState: {
        current: {
            latitude: -7.807985219836422,
            longitude: 110.3589963912964,
            detail: []
        },
        origin: {
            latitude: -7.807985219836422,
            longitude: 110.3589963912964,
            detail: []
        },
        destination: {
            latitude: -7.7831542788869275,
            longitude: 110.38916587829591,
            detail: []
        },
        click: {
            latitude: 0,
            longitude: 0,
            detail: []
        },
        clickFlag: false
    },
    reducers: {
        setCurrent: (state, action) => {
            state.current = action.payload
        },
        setOrigin: (state, action) => {
            state.origin = action.payload
        },
        setDestination: (state, action) => {
            state.destination = action.payload
        },
        setClick: (state, action) => {
            state.click = action.payload
        },
        setClickFlag: (state, action) => {
            state.clickFlag = action.payload
        }
    }
})

export const selectLocation = {
    current: (state) => state.location.current,
    origin: (state) => state.location.origin,
    destination: (state) => state.location.destination,
    click: (state) => state.location.click,
    clickFlag: (state) => state.location.clickFlag
}

export const { setCurrent, setOrigin, setDestination, setClick, setClickFlag } = locationSlice.actions

export default locationSlice.reducer
