import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import * as NomiService from '../services/Nomi.service'
import * as NomiSlice from '../store/slices/Nomi.slice'

const Nomi = () => {
    const [dataInput, setInput] = useState('')
    const dispatch = useDispatch()
    const NomiError = useSelector(NomiSlice.selectNomi.isError)
    const NomiFetch = useSelector(NomiSlice.selectNomi.isFetching)
    const NomiStatus = useSelector(NomiSlice.selectNomi.status)
    const NomiSearch = useSelector(NomiSlice.selectNomi.search)
    const NomiReverse = useSelector(NomiSlice.selectNomi.reverse)

    const handler = () => {
        dispatch(NomiService.status())
    }

    const inputHandler = (payload) => {
        setInput(payload.target.value)
    }

    const searchHandler = () => {
        console.log('find', dataInput)
        dispatch(NomiService.search({
            query: dataInput
        })).then(payload => {
            console.log(payload)
        })
    }

    if (Object.keys(NomiStatus).length !== 0) {
        if (NomiStatus.status == 0) {
            console.log('Nomi Works!')
        } else {
            console.log(`Something's wrong i can feel it.`)
        }
    }

    if(NomiSearch != ''){
        console.log('ooh')
        console.log(NomiSearch)
    }

    return (
        <div>
            <h1>Nomi</h1>
            <div className='input-group'>
                <button className='btn btn-outline-dark' onClick={handler}>
                    Status
                </button>
                <input
                    className='form-control'
                    type='text'
                    placeholder='Location'
                    value={dataInput}
                    onChange={inputHandler}
                />
                <button
                    className='btn btn-outline-dark'
                    onClick={searchHandler}>
                    Search
                </button>
            </div>
        </div>
    )
}

export default Nomi
