import { createSlice } from '@reduxjs/toolkit'
import { status, search, reverse } from '../../services/Nomi.service'
import {
    fulfilled,
    fulfilledCustom,
    pending,
    rejected
} from '../reducers/extra.reducer'

export const nomiSlice = createSlice({
    name: 'nomi',
    initialState: {
        data: {},
        status: {},
        search: [],
        reverse: [],
        isError: false,
        isFetching: false
    },
    extraReducers: {
        [status.fulfilled]: fulfilledCustom('status'),
        [status.pending]: pending,
        [status.rejected]: rejected,

        [search.fulfilled]: fulfilledCustom('search'),
        [search.pending]: pending,
        [search.rejected]: rejected,

        [reverse.fulfilled]: fulfilledCustom('reverse'),
        [reverse.pending]: pending,
        [reverse.rejected]: rejected
    }
})

export const selectNomi = {
    data: (state) => state.nomi.data,
    status: (state) => state.nomi.status,
    search: (state) => state.nomi.search,
    reverse: (state) => state.nomi.reverse,
    isFetching: (state) => state.nomi.isFetching,
    isError: (state) => state.nomi.isError
}

export const {} = nomiSlice.actions

export default nomiSlice.reducer
