import { createSlice } from '@reduxjs/toolkit'
import { get } from '../../services/Route.service'
import { fulfilled, pending, rejected } from '../reducers/extra.reducer'

export const routeSlice = createSlice({
    name: 'route',
    initialState: {
        data: [],
        isFetching: false,
        isError: false
    },
    reducers: {
        set: (state, action) => {
            state.data = action.payload
        },
        clear: (state) => {
            state.data = []
        }
    },
    extraReducers: {
        [get.fulfilled]: fulfilled,
        [get.pending]: pending,
        [get.rejected]: rejected
    }
})

export const selectRoute = {
    data: (state) => state.route.data,
    isFetching: (state) => state.route.isFetching,
    isError: (state) => state.route.isError
}

export const { set, clear } = routeSlice.actions

export default routeSlice.reducer
