import { createSlice } from '@reduxjs/toolkit'

export const viewSlice = createSlice({
    name: 'view',
    initialState: {
        pane: true,
        paneExtra: false,
        paneContent: 'shelter',
        isDetail: false
    },
    reducers: {
        set: (state, action) => {
            state.pane = action.payload
        },
        setExtra: (state, action) => {
            state.paneExtra = action.payload
        },
        setContent: (state, action) => {
            state.paneContent = action.payload
        },
        setDetail: (state, action) => {
            state.isDetail = action.payload
        }
    }
})

export const selectView = {
    pane: (state) => state.view.pane,
    paneExtra: (state) => state.view.paneExtra,
    paneContent: (state) => state.view.paneContent,
    isDetail: (state) => state.view.isDetail
}

export const { set, setExtra, setContent, setDetail } = viewSlice.actions

export default viewSlice.reducer
