import { Card, Col, FloatingLabel, Form, Row } from 'react-bootstrap'

const Filter = () => {
    return (
        <Card className={'sidebar__filter'}>
            <Card.Body>
                <Row className={'g-2'}>
                    <Col>
                        <FloatingLabel controlId='labelLayanan' label='Layanan'>
                            <Form.Select aria-label='Layanan'>
                                <option value='0'>Semua</option>
                                <option value='1'>Trans Jogja</option>
                                <option value='2'>Teman Bus</option>
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                    <Col>
                        <FloatingLabel
                            controlId='labelJenis'
                            label='Jenis Shelter'>
                            <Form.Select aria-label='Jenis Shelter'>
                                <option value='0'>Semua</option>
                                <option value='1'>Shelter</option>
                                <option value='2'>Halte Portable</option>
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                </Row>
            </Card.Body>
        </Card>
    )
}

export default Filter
