import { createSlice } from '@reduxjs/toolkit'
import {
    get,
    getByID,
    getStartShelter,
    getPath
} from '../../services/Corridor.service'
import {
    fulfilled,
    fulfilledCustom,
    pending,
    rejected
} from '../reducers/extra.reducer'

export const corridorSlice = createSlice({
    name: 'corridor',
    initialState: {
        data: [],
        start: [],
        path: [],
        isFetching: false,
        isError: false
    },
    reducers: {
        set: (state, action) => {
            state.data = action.payload
        },
        clear: (state) => {
            state.data = []
        },
        clearPath: (state) => {
            state.path = []
        }
    },
    extraReducers: {
        [get.fulfilled]: fulfilled,
        [get.pending]: pending,
        [get.rejected]: rejected,

        [getByID.fulfilled]: fulfilled,
        [getByID.pending]: pending,
        [getByID.rejected]: rejected,

        [getStartShelter.fulfilled]: fulfilledCustom('start'),
        [getStartShelter.pending]: pending,
        [getStartShelter.rejected]: rejected,

        [getPath.fulfilled]: fulfilledCustom('path'),
        [getPath.pending]: pending,
        [getPath.rejected]: rejected
    }
})

export const selectCorridor = {
    data: (state) => state.corridor.data,
    path: (state) => state.corridor.path,
    isFetching: (state) => state.corridor.isFetching,
    isError: (state) => state.corridor.isError
}

export const { set, clear, clearPath } = corridorSlice.actions

export default corridorSlice.reducer
