import { Button, Card, FloatingLabel, Form } from 'react-bootstrap'
import Icon from '../../partials/Icon'

const Direction = (props) => {
    return (
        <Card
            className={
                props.show
                    ? 'sidebar__direction animate__animated animate__fadeIn mb-2 d-none'
                    : 'sidebar__direction animate__animated animate__fadeIn mb-2'
            }>
            <Card.Body>
                <FloatingLabel
                    controlId='labelAsal'
                    label='Asal'
                    className='mb-1'>
                    <Form.Control type='text' placeholder='Asal' />
                </FloatingLabel>
                <FloatingLabel
                    controlId='labelTujuan'
                    label='Tujuan'
                    className='mb-2'>
                    <Form.Control type='text' placeholder='Tujuan' />
                </FloatingLabel>
                <Button variant='outline-dark' className='w-100'>
                    <Icon.Search></Icon.Search>
                    Cari
                </Button>
            </Card.Body>
        </Card>
    )
}

export default Direction
