import { useRouteMatch } from 'react-router'
import Map from '../components/_Map/Map'
import Pane from '../components/_Pane/Pane'
import { Toaster, ToastCreator } from '../components/partials/Toast'
import { useTitle } from '../components/partials/DocumentTitle'

const Ref = () => {
    // console.log('ref')

    const BASE_PATH = '/brt-route/ref'
    const match = useRouteMatch({
        path: [
            `${BASE_PATH}`,
            `${BASE_PATH}/`,
            `${BASE_PATH}/@:center`,
            `${BASE_PATH}/@:center/`,
            `${BASE_PATH}/:type(corridors|shelters)`,
            `${BASE_PATH}/:type(corridors|shelters)/`,
            `${BASE_PATH}/:type(corridors|shelters)/@:center`,
            `${BASE_PATH}/:type(corridors|shelters)/@:center/`,
            `${BASE_PATH}/:type(corridors|shelters)/:uuid`,
            `${BASE_PATH}/:type(corridors|shelters)/:uuid/`,
            `${BASE_PATH}/:type(corridors|shelters)/:uuid/@:center`,
            `${BASE_PATH}/:type(corridors|shelters)/:uuid/@:center/`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/@:center`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/@:center/`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/:routeId`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/:routeId/`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/:routeId/@:center`,
            `${BASE_PATH}/:type(routes)/:originCoords/:targetCoords/:routeId/@:center/`
        ],
        strict: true,
        sensitive: true,
        exact: true
    })

    // console.log('params', match.params)

    useTitle(
        match && match.params && match.params.type
            ? match.params.type + ' | BRT Trans Jogja'
            : 'BRT Trans Jogja'
    )

    const data = [
        {
            title: 'Bruh',
            status: 'error',
            message: 'message'
        }
    ]

    return (
        <div className='main'>
            <Map>
                {match && <Pane match={match} />}
                <Toaster>
                    {data.map((item, index) => {
                        return (
                            <ToastCreator
                                title={item.title}
                                status={item.status}
                                message={item.message}
                                key={index}></ToastCreator>
                        )
                    })}
                </Toaster>
            </Map>
        </div>
    )
}

export default Ref
