import { Marker, Popup } from 'react-leaflet'

import * as MapIcon from './Icon'

const MarkerUser = () => {
    return (
        <Marker position={[51.505, -0.06]} icon={MapIcon.User}>
            <Popup>user</Popup>
        </Marker>
    )
}

const MarkerOrigin = () => {
    return (
        <Marker position={[51.505, -0.07]} icon={MapIcon.Origin}>
            <Popup>origin</Popup>
        </Marker>
    )
}

const MarkerTarget = () => {
    return (
        <Marker position={[51.505, -0.08]} icon={MapIcon.Target}>
            <Popup>target</Popup>
        </Marker>
    )
}

const MarkerShelter = () => {
    return (
        <Marker position={[51.505, -0.09]} icon={MapIcon.Shelter}>
            <Popup>shelter</Popup>
        </Marker>
    )
}

const MarkerSelected = () => {
    return (
        <Marker position={[51.505, -0.1]} icon={MapIcon.Selected}>
            <Popup>selected</Popup>
        </Marker>
    )
}

const MarkerRouteStart = () => {
    return (
        <Marker position={[51.505, -0.11]} icon={MapIcon.RouteStart}>
            <Popup>route start</Popup>
        </Marker>
    )
}

const MarkerRouteEnd = () => {
    return (
        <Marker position={[51.505, -0.12]} icon={MapIcon.RouteEnd}>
            <Popup>route end</Popup>
        </Marker>
    )
}

const MarkerRouteLoop = () => {
    return (
        <Marker position={[51.505, -0.13]} icon={MapIcon.RouteLoop}>
            <Popup>route loop</Popup>
        </Marker>
    )
}

const Markers = (props) => {
    return (
        <>
            <MarkerUser></MarkerUser>
            <MarkerOrigin></MarkerOrigin>
            <MarkerTarget></MarkerTarget>
            <MarkerShelter></MarkerShelter>
            <MarkerSelected></MarkerSelected>
            <MarkerRouteStart></MarkerRouteStart>
            <MarkerRouteEnd></MarkerRouteEnd>
            <MarkerRouteLoop></MarkerRouteLoop>
        </>
    )
}

export {
    Markers,
    MarkerUser,
    MarkerOrigin,
    MarkerTarget,
    MarkerShelter,
    MarkerSelected,
    MarkerRouteStart,
    MarkerRouteEnd,
    MarkerRouteLoop
}
