import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faBus,
    faRoad,
    faSearch,
    faLocationDot,
    faPerson,
    faCircleDot,
    faBars,
    faLocationCrosshairs,
    faDiamondTurnRight,
    faChevronLeft
} from '@fortawesome/free-solid-svg-icons'

const Bus = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--bus ' + props.extraClass
                : 'icon icon--bus me-2'
        }
        icon={faBus}
    />
)

const Road = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--road ' + props.extraClass
                : 'icon icon--road me-2'
        }
        icon={faRoad}
    />
)

const Search = () => (
    <FontAwesomeIcon className='icon icon--search me-2' icon={faSearch} />
)

const Destination = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--destination me-2 ' + props.extraClass
                : 'icon icon--destination me-2'
        }
        icon={faLocationDot}
    />
)

const Source = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--source me-2 ' + props.extraClass
                : 'icon icon--source me-2'
        }
        icon={faCircleDot}
    />
)

const User = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--user me-2 ' + props.extraClass
                : 'icon icon--user me-2'
        }
        icon={faPerson}
    />
)

const Bar = (props) => (
    <FontAwesomeIcon className='icon icon--bar' icon={faBars} />
)

const Location = (props) => (
    <FontAwesomeIcon
        className={
            props.extraClass
                ? 'icon icon--location me-2 ' + props.extraClass
                : 'icon icon--location'
        }
        icon={faLocationCrosshairs}
    />
)

const Route = (props) => (
    <FontAwesomeIcon className='icon icon--route' icon={faDiamondTurnRight} />
)

const Back = (props) => (
    <FontAwesomeIcon className='icon icon--back' icon={faChevronLeft} />
)

const Icon = {
    Bus,
    Road,
    Search,
    Destination,
    Source,
    User,
    Bar,
    Location,
    Route,
    Back
}

export default Icon
