import { useHistory } from 'react-router'
import { Button, ButtonGroup, Card } from 'react-bootstrap'
import Icon from '../../partials/Icon'

const Navigation = (props) => {
    const history = useHistory()

    const handleList = (type) => history.push('/brt-route/ref/' + type)
    const handleDetail = (type, uuid) =>
        history.push('/brt-route/ref/' + type + '/' + uuid)
    const handleRouteList = () =>
        history.push('/brt-route/ref/routes/1471,7091/1472,7092')
    const handleRouteDetail = () =>
        history.push('/brt-route/ref/routes/1471,7091/1472,7092/1')
    const handleBack = () => history.goBack()

    return (
        <Card className={'sidebar__navigation mb-2'}>
            <Card.Body>
                <ButtonGroup className='w-100 mb-1'>
                    <Button variant='outline-dark' className={'shadow-none'}>
                        <Icon.Bar></Icon.Bar>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={handleBack}>
                        <Icon.Back></Icon.Back>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={props.routeHandle}>
                        <Icon.Route></Icon.Route>
                    </Button>
                </ButtonGroup>

                <ButtonGroup className='w-100 mb-1'>
                    <Button variant='outline-dark' disabled>
                        L:
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleList('corridors')}>
                        <Icon.Road></Icon.Road>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleList('shelters')}>
                        <Icon.Bus></Icon.Bus>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleRouteList()}>
                        <Icon.Route></Icon.Route>
                    </Button>
                </ButtonGroup>

                <ButtonGroup className='w-100 mb-1'>
                    <Button variant='outline-dark' disabled>
                        D:
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleDetail('corridors', '1')}>
                        <Icon.Road></Icon.Road>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleDetail('shelters', '2')}>
                        <Icon.Bus></Icon.Bus>
                    </Button>
                    <Button
                        variant='outline-dark'
                        className={'shadow-none'}
                        onClick={() => handleRouteDetail()}>
                        <Icon.Route></Icon.Route>
                    </Button>
                </ButtonGroup>
            </Card.Body>
        </Card>
    )
}

export default Navigation
