import { createSlice } from '@reduxjs/toolkit'
import {
    get,
    getByID,
    getNearby,
    getCorridor
} from '../../services/Shelter.service'
import { fulfilled, fulfilledCustom, pending, rejected } from '../reducers/extra.reducer'

export const shelterSlice = createSlice({
    name: 'shelter',
    initialState: {
        data: [],
        nearby: [],
        isFetching: false,
        isError: false
    },
    reducers: {
        set: (state, action) => {
            state.data = action.payload
        },
        clear: (state) => {
            state.data = []
        }
    },
    extraReducers: {
        [get.fulfilled]: fulfilled,
        [get.pending]: pending,
        [get.rejected]: rejected,

        [getByID.fulfilled]: fulfilled,
        [getByID.pending]: pending,
        [getByID.rejected]: rejected,

        [getNearby.fulfilled]: fulfilledCustom('nearby'),
        [getNearby.pending]: pending,
        [getNearby.rejected]: rejected,

        [getCorridor.fulfilled]: fulfilled,
        [getCorridor.pending]: pending,
        [getCorridor.rejected]: rejected
    }
})

export const selectShelter = {
    data: (state) => state.shelter.data,
    nearby: (state) => state.shelter.nearby,
    isFetching: (state) => state.shelter.isFetching,
    isError: (state) => state.shelter.isError
}

export const { set, clear } = shelterSlice.actions

export default shelterSlice.reducer
