import L from 'leaflet'

import 'beautifymarker/leaflet-beautify-marker-icon.css'
import 'beautifymarker/leaflet-beautify-marker-icon.js'

const User = L.BeautifyIcon.icon({
    icon: 'person',
    iconShape: 'marker',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const Origin = L.BeautifyIcon.icon({
    icon: 'road',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const Target = L.BeautifyIcon.icon({
    icon: 'circle',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const Shelter = L.BeautifyIcon.icon({
    icon: 'bus',
    iconShape: 'marker',
    borderColor: '#b3334f',
    textColor: '#b3334f',
    customClasses: 'animate__animated animate__fadeIn'
})
const Selected = L.BeautifyIcon.icon({
    icon: 'circle',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const RouteStart = L.BeautifyIcon.icon({
    icon: 'circle',
    iconShape: 'marker',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const RouteEnd = L.BeautifyIcon.icon({
    icon: 'circle',
    iconShape: 'marker',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})
const RouteLoop = L.BeautifyIcon.icon({
    icon: 'circle',
    iconShape: 'marker',
    borderColor: '#b3334f',
    textColor: '#b3334f'
})

export {
    User,
    Origin,
    Target,
    Shelter,
    Selected,
    RouteStart,
    RouteEnd,
    RouteLoop
}