import React from 'react'
import { ListGroup, Badge, Row, Col } from 'react-bootstrap'

const Corridor = (props) => {
    // console.log('pane list corridor')

    return (
        <div className='sidebar__list__corridor animate__animated animate__fadeIn'>
            <ListGroup>
                <ListGroup.Item className='sidebar__list__item animate__animated animate__fadeIn'>
                    <Row className={'gx-0 gy-1'}>
                        <Col sm={4} xs={12}>
                            <h5 className='sidebar__list__header me-sm-2 mb-0'>
                                <Badge bg='danger'>Koridor 1A</Badge>
                            </h5>
                        </Col>
                        <Col>
                            <div className='sidebar__list__body ms-1 text-center text-sm-start'>
                                <h6 className='sidebar__list__title mb-1'>
                                    Kaliurang - Jombor
                                </h6>
                                <h6 className='sidebar__list__subtitle text-muted small mb-0'>
                                    Trans Jogja
                                </h6>
                            </div>
                        </Col>
                    </Row>
                </ListGroup.Item>
            </ListGroup>
        </div>
    )
}

const CompMemo = React.memo(Corridor)

export { CompMemo as default }
