import { Route, Switch } from 'react-router'
// import Index from './Index'
import Nomi from './Nomi'
import NotFound from './404'
import MapTest from './MapTest'
import Ref from './Ref'

const Routes = () => {
    const path = '/brt-route'
    const routes = [
        {
            path: '',
            component: NotFound,
            isExact: true,
            isChildren: false
        },
        {
            path: 'ref',
            component: Ref,
            isExact: false,
            isChildren: false
        },
        {
            path: 'shelters',
            component: NotFound,
            isExact: false,
            isChildren: false // true
        },
        {
            path: 'corridors',
            component: NotFound,
            isExact: false,
            isChildren: false // true
        },
        {
            path: 'routes',
            component: NotFound,
            isExact: false,
            isChildren: false // true
        },
        {
            path: 'nomi',
            component: Nomi,
            isExact: false,
            isChildren: false
        },
        {
            path: 'map-test',
            component: MapTest,
            isExact: false,
            isChildren: false
        },
        {
            path: '*',
            component: NotFound,
            isExact: false,
            isChildren: false
        }
    ]
    return (
        <Switch>
            {routes.map((item, index) => (
                <Route
                    exact={item.isExact}
                    path={`${path}/${item.path}`}
                    component={!item.isChildren && item.component}
                    children={item.isChildren && item.component}
                    key={index}
                />
            ))}
        </Switch>
    )
}

export default Routes
