import { createSlice } from '@reduxjs/toolkit'

export const flagSlice = createSlice({
    name: 'flag',
    initialState: {
        pane: {
            case: 'route',           // list, detail, route, default
            list: 'corridor',       // shelter, corridor, route, default
            detail: 'corridor',     // shelter, corridor, route, default
            style: 'default'       // default, expand
        },
        control: {
            style: 'default'       // default, expand
        }
    },
    reducers: {
        setPaneCase: (state, action) => {
            state.pane.case = action.payload
        },
        setPaneList: (state, action) => {
            state.pane.list = action.payload
        },
        setPaneDetail: (state, action) => {
            state.pane.detail = action.payload
        },
        setPaneStyle: (state, action) => {
            state.pane.style = action.payload
        },
        setControlStyle: (state, action) =>{
            state.control.style = action.payload
        }
    }
})

export const selectFlag = {
    paneCase: (state) => state.flag.pane.case,
    paneList: (state) => state.flag.pane.list,
    paneDetail: (state) => state.flag.pane.detail,
    paneStyle: (state) => state.flag.pane.style,
    controlStyle: (state) => state.flag.control.style
}

export const { setPaneCase, setPaneList, setPaneDetail, setPaneStyle, setControlStyle } =
    flagSlice.actions

export default flagSlice.reducer
