import React, { useRef, useEffect, useState } from 'react'
import {Container } from 'react-bootstrap'
import L from 'leaflet'

import Navigation from './Navigation/Navigation'
import Direction from './Navigation/Direction'
import Filter from './Navigation/Filter'
import PaneContent from './Content/PaneContent'

const Pane = ({ match }) => {
    // console.log('pane')

    const ref = useRef(null)

    const [hideRoute, setRoute] = useState(true)
    const routeHandle = () => setRoute(!hideRoute)

    useEffect(() => {
        L.DomEvent.disableClickPropagation(ref.current)
        L.DomEvent.disableScrollPropagation(ref.current)
    })

    return (
        <Container className={'sidebar py-2'} ref={ref}>
            <div className='sidebar__header mb-2'>
                <Navigation routeHandle={routeHandle}></Navigation>
                <Direction show={hideRoute}></Direction>
                <Filter></Filter>
            </div>
            <PaneContent match={match} />
        </Container>
    )
}

const CompMemo = React.memo(Pane)

export { CompMemo as default }
